<template>
  <div class="listing-detail">
    <div class="listing-detail__container">
      <div v-if="isLoading" class="listing-detail__loader-container">
        <div class="listing-detail__loader"></div>
      </div>
      
      <div v-else-if="!listing" class="listing-detail__not-found">
        <h2 class="listing-detail__not-found-title">Листинг не найден</h2>
        <p class="listing-detail__not-found-message">NFT листинг, который вы ищете, не существует или был удален.</p>
        <router-link to="/nft-market" class="listing-detail__back-button">Вернуться на маркетплейс</router-link>
      </div>
      
      <div v-else class="listing-detail__content">
        <div class="listing-detail__grid">
          <!-- Левая колонка - изображение NFT -->
          <div class="listing-detail__image-section">
            <div class="listing-detail__image-container">
              <img 
                v-if="listing.imageUrl" 
                :src="listing.imageUrl" 
                :alt="listing.name" 
                class="listing-detail__image"
              />
              <div v-else class="listing-detail__image-placeholder">
                <i class="fas fa-image listing-detail__placeholder-icon"></i>
              </div>
            </div>
          </div>
          
          <!-- Правая колонка - информация о листинге -->
          <div class="listing-detail__info-section">
            <div class="listing-detail__header">
              <div>
                <h1 class="listing-detail__title">{{ listing.name || `NFT #${listing.mintAddress.substring(0, 8)}` }}</h1>
                <p class="listing-detail__address">{{ shortenAddress(listing.mintAddress) }}</p>
              </div>
              <div class="listing-detail__status" v-if="listing.status !== 'active'">
                <span 
                  class="listing-detail__status-badge"
                  :class="{
                    'listing-detail__status-badge--sold': listing.status === 'sold',
                    'listing-detail__status-badge--cancelled': listing.status === 'cancelled',
                    'listing-detail__status-badge--expired': listing.status === 'expired'
                  }"
                >
                  {{ listing.status }}
                </span>
              </div>
            </div>
            
            <div class="listing-detail__seller-info" v-if="listing.seller && listing.seller.userTag">
              <h3 class="listing-detail__seller-title">Seller</h3>
              <p class="listing-detail__seller-tag">{{ listing.seller.userTag }}</p>
            </div>
            
            <div class="listing-detail__seller">
              <p class="listing-detail__seller-label">Продавец</p>
              <p class="listing-detail__seller-value">
                {{ listing.seller ? listing.seller.displayName || listing.seller.userTag || shortenAddress(listing.sellerWallet) : shortenAddress(listing.sellerWallet) }}
              </p>
            </div>
            
            <div class="listing-detail__price-section">
              <div class="listing-detail__price-block">
                <p class="listing-detail__price-label">Текущая цена</p>
                <p class="listing-detail__price-value">{{ formatPrice(listing.highestBid || listing.initialPrice) }} SOL</p>
              </div>
              
              <div class="listing-detail__price-block" v-if="listing.instantPrice">
                <p class="listing-detail__price-label">Цена мгновенной покупки</p>
                <p class="listing-detail__price-value">{{ formatPrice(listing.instantPrice) }} SOL</p>
              </div>
            </div>
            
            <div class="listing-detail__time-block">
              <p class="listing-detail__time-label">Аукцион завершается</p>
              <p class="listing-detail__time-value">{{ formatTimeRemaining(listing.endTime) }}</p>
              <p class="listing-detail__time-date">{{ formatDate(listing.endTime) }}</p>
            </div>
            
            <div class="listing-detail__bid-section" v-if="listing.status === 'active'">
              <div class="listing-detail__highest-bid" v-if="listing.highestBid">
                <p class="listing-detail__bid-label">Текущая ставка</p>
                <p class="listing-detail__bid-value">{{ formatPrice(listing.highestBid) }} SOL</p>
                <p class="listing-detail__bidder">
                  от {{ listing.highestBidder ? listing.highestBidder.userTag || shortenAddress(listing.highestBidder) : shortenAddress(listing.highestBidder) }}
                  <span v-if="isCurrentUserHighestBidder" class="listing-detail__bidder-you">(Вы)</span>
                </p>
              </div>
              
              <div class="listing-detail__actions">
                <button 
                  v-if="canBid"
                  @click="showBidModal = true"
                  class="listing-detail__bid-button"
                >
                  Сделать ставку
                </button>
                <button 
                  v-if="canBuyNow"
                  @click="buyNow"
                  class="listing-detail__buy-button"
                >
                  Купить сейчас за {{ formatPrice(listing.instantPrice) }} SOL
                </button>
                <button 
                  v-if="canCancel"
                  @click="showCancelModal = true"
                  class="listing-detail__cancel-button"
                >
                  Отменить листинг
                </button>
              </div>
            </div>
            
            <div class="listing-detail__description" v-if="listing.nftDetails && listing.nftDetails.description">
              <h3 class="listing-detail__description-title">Описание</h3>
              <p class="listing-detail__description-text">{{ listing.nftDetails.description }}</p>
            </div>
          </div>
        </div>
      </div>
      
      <div class="listing-detail__back-link-container">
        <router-link to="/nft-market" class="listing-detail__back-link">
          <i class="fas fa-arrow-left listing-detail__back-icon"></i> Назад к маркетплейсу
        </router-link>
      </div>
    </div>
    
    <!-- Модальные окна -->
    <PlaceBidModal 
      v-if="showBidModal && listing" 
      :listing="listing"
      :is-submitting="isPlacingBid"
      @close="showBidModal = false"
      @submit="handlePlaceBid"
    />
    
    <CancelListingModal 
      v-if="showCancelModal && listing" 
      :is-submitting="isCancelling"
      @close="showCancelModal = false"
      @confirm="handleCancelListing"
    />
  </div>
