<script>
import { ref, computed, onMounted } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useWallet } from 'solana-wallets-vue';
import axios from 'axios';

import PlaceBidModal from '@/components/nft-market/PlaceBidModal.vue';
import CancelListingModal from '@/components/nft-market/CancelListingModal.vue';

export default {
  name: 'NftListingDetailPage',
  components: {
    PlaceBidModal,
    CancelListingModal
  },
  setup() {
    const route = useRoute();
    const router = useRouter();
    const { wallet, connected, publicKey } = useWallet();
    
    // Состояние данных
    const listing = ref(null);
    const mintAddress = route.params.mintAddress;
    
    // Состояние UI
    const isLoading = ref(true);
    const isPlacingBid = ref(false);
    const isCancelling = ref(false);
    const showBidModal = ref(false);
    const showCancelModal = ref(false);
    
    // Вычисляемые свойства
    const walletAddress = computed(() => {
      return publicKey.value ? publicKey.value.toString() : '';
    });
    
    const isCurrentUserHighestBidder = computed(() => {
      return listing.value && 
             listing.value.highestBidder && 
             walletAddress.value === listing.value.highestBidder;
    });
    
    const canBid = computed(() => {
      return connected.value && 
             listing.value && 
             listing.value.status === 'active' && 
             listing.value.sellerWallet !== walletAddress.value &&
             new Date(listing.value.endTime) > new Date();
    });
    
    const canBuyNow = computed(() => {
      return canBid.value && 
             listing.value.instantPrice;
    });
    
    const canCancel = computed(() => {
      return connected.value && 
             listing.value && 
             listing.value.status === 'active' && 
             listing.value.sellerWallet === walletAddress.value;
    });
    
    // Методы для загрузки данных
    const loadListing = async () => {
      try {
        isLoading.value = true;
        const response = await axios.get(`/nft-market/listings/mint/${mintAddress}`);
        listing.value = response.data.data;
      } catch (error) {
        console.error('Error loading listing:', error);
        listing.value = null;
      } finally {
        isLoading.value = false;
      }
    };
    
    // Обработчики событий
    const buyNow = () => {
      if (!canBuyNow.value) return;
      
      // Устанавливаем ставку равной цене мгновенной покупки
      showBidModal.value = true;
    };
    
    const handlePlaceBid = async (data) => {
      try {
        isPlacingBid.value = true;
        
        // Размещаем ставку на сервере
        const response = await axios.post('/nft-market/bid', {
          mintAddress: data.mintAddress,
          bidAmount: data.amount
        });
        
        // В реальной реализации здесь будет подписание транзакции
        // Для примера просто закрываем модальное окно и обновляем данные
        
        showBidModal.value = false;
        await loadListing();
        
      } catch (error) {
        console.error('Error placing bid:', error);
        alert('Failed to place bid. Please try again.');
      } finally {
        isPlacingBid.value = false;
      }
    };
    
    const handleCancelListing = async () => {
      try {
        isCancelling.value = true;
        
        // Отменяем листинг на сервере
        const response = await axios.post('/nft-market/listings/cancel', {
          mintAddress: listing.value.mintAddress
        });
        
        // В реальной реализации здесь будет подписание транзакции
        // Для примера просто закрываем модальное окно и обновляем данные
        
        showCancelModal.value = false;
        await loadListing();
        
      } catch (error) {
        console.error('Error cancelling listing:', error);
        alert('Failed to cancel listing. Please try again.');
      } finally {
        isCancelling.value = false;
      }
    };
    
    // Вспомогательные функции
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(3);
    };
    
    const formatTimeRemaining = (endTime) => {
      const end = new Date(endTime);
      const now = new Date();
      
      if (end <= now) {
        return 'Ended';
      }
      
      const diff = end - now;
      const days = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      
      if (days > 0) {
        return `${days}d ${hours}h ${minutes}m`;
      } else if (hours > 0) {
        return `${hours}h ${minutes}m`;
      } else {
        return `${minutes}m`;
      }
    };
    
    const formatDate = (dateString) => {
      const date = new Date(dateString);
      return date.toLocaleString();
    };
    
    const shortenAddress = (address) => {
      if (!address) return '';
      return `${address.substring(0, 4)}...${address.substring(address.length - 4)}`;
    };
    
    // Жизненный цикл
    onMounted(() => {
      loadListing();
    });
    
    return {
      listing,
      isLoading,
      isPlacingBid,
      isCancelling,
      showBidModal,
      showCancelModal,
      isCurrentUserHighestBidder,
      canBid,
      canBuyNow,
      canCancel,
      buyNow,
      handlePlaceBid,
      handleCancelListing,
      formatPrice,
      formatTimeRemaining,
      formatDate,
      shortenAddress
    };
  }
};
</script>

