<template>
  <div class="bid-modal">
    <div class="bid-modal__box">
      <h3 class="bid-modal__title">Place Bid</h3>
      
      <div class="bid-modal__nft-preview">
        <div class="bid-modal__nft-image-container">
          <img 
            v-if="listing.imageUrl" 
            :src="listing.imageUrl" 
            :alt="listing.name" 
            class="bid-modal__nft-image"
          />
          <div v-else class="bid-modal__nft-placeholder">
            <i class="fas fa-image bid-modal__nft-placeholder-icon"></i>
          </div>
        </div>
        <h4 class="bid-modal__nft-name">{{ listing.name || `NFT #${listing.mintAddress.substring(0, 8)}` }}</h4>
      </div>
      
      <form @submit.prevent="submitBid" class="bid-modal__form">
        <div class="bid-modal__form-group">
          <label class="bid-modal__form-label">
            <span class="bid-modal__form-label-text">Current Price</span>
          </label>
          <input 
            :value="formatPrice(listing.highestBid || listing.initialPrice)" 
            type="text" 
            class="bid-modal__form-input bid-modal__form-input--disabled" 
            disabled
          />
        </div>
        
        <div class="bid-modal__form-group">
          <label class="bid-modal__form-label">
            <span class="bid-modal__form-label-text">Your Bid (SOL)</span>
          </label>
          <input 
            v-model="bidAmount" 
            type="number" 
            step="0.01" 
            :min="minBidAmount" 
            placeholder="Enter bid amount" 
            class="bid-modal__form-input" 
            required
          />
          <span class="bid-modal__form-help">Minimum bid: {{ minBidAmount }} SOL</span>
        </div>
        
        <div class="bid-modal__fee-summary">
          <h4 class="bid-modal__fee-title">Fees</h4>
          <div class="bid-modal__fee-row">
            <span>Marketplace Fee (5%)</span>
            <span>{{ calculateFee() }} SOL</span>
          </div>
          <div class="bid-modal__fee-row bid-modal__fee-row--total">
            <span>Total</span>
            <span>{{ calculateTotal() }} SOL</span>
          </div>
        </div>
        
        <div class="bid-modal__actions">
          <button type="button" class="bid-modal__button bid-modal__button--cancel" @click="$emit('close')">Cancel</button>
          <button 
            type="submit" 
            class="bid-modal__button bid-modal__button--submit" 
            :disabled="isSubmitting || !isFormValid"
          >
            <span v-if="isSubmitting" class="bid-modal__spinner"></span>
            Place Bid
          </button>
        </div>
      </form>
    </div>
    <div class="bid-modal__backdrop" @click="$emit('close')"></div>
  </div>
</template>

<script>
export default {
  name: 'PlaceBidModal',
  props: {
    listing: {
      type: Object,
      required: true
    },
    isSubmitting: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      bidAmount: ''
    };
  },
  computed: {
    minBidAmount() {
      const currentBid = this.listing.highestBid || this.listing.initialPrice;
      return (parseFloat(currentBid) + 0.01).toFixed(2);
    },
    isFormValid() {
      return (
        this.bidAmount && 
        parseFloat(this.bidAmount) >= parseFloat(this.minBidAmount)
      );
    }
  },
  methods: {
    formatPrice(price) {
      return parseFloat(price).toFixed(3);
    },
    calculateFee() {
      if (!this.bidAmount) return '0';
      const fee = parseFloat(this.bidAmount) * 0.05;
      return fee.toFixed(3);
    },
    calculateTotal() {
      if (!this.bidAmount) return '0';
      const amount = parseFloat(this.bidAmount);
      const fee = amount * 0.05;
      return (amount + fee).toFixed(3);
    },
    submitBid() {
      if (!this.isFormValid) return;
      
      this.$emit('submit', {
        mintAddress: this.listing.mintAddress,
        amount: parseFloat(this.bidAmount)
      });
    }
  },
  mounted() {
    // Устанавливаем начальное значение ставки равным минимальной ставке
    this.bidAmount = this.minBidAmount;
  }
};
</script>

